	margin: -35px auto 0;
	position: absolute;
	z-index: 3;
	top: 50%;
	width: 100%;
}

.slides-navigation a {
	position: absolute;
	background: #fff;
	display: block;
	height: 40px;
	width: 40px;
	top: 50%;
	margin: -20px 10px 0;
	padding: 0;
	opacity: 0;
	text-align: center;
	line-height: 40px;
	font-size: 17px;
	color: #111;
	-webkit-border-radius: 2px;
