	     -o-transition: all 0.3s cubic-bezier(0.000, 0.000, 0.580, 1.000);
	        transition: all 0.3s cubic-bezier(0.000, 0.000, 0.580, 1.000);
}

.slides-pagination a.current {
	-webkit-transform: scale(1.3);
	   -moz-transform: scale(1.3);
