}

.input-sm, .form-horizontal .form-group-sm .form-control {
	height: 29px;
	font-size: 10px;
}

/* ------------------------------------------------------------------
Buttons
------------------------------------------------------------------- */

.btn {
	text-transform: uppercase;
	letter-spacing: 2px;
	font-size: 11px;
	padding: 8px 37px;
	-webkit-border-radius: 0;
	   -moz-border-radius: 0;
	     -o-border-radius: 0;
	        border-radius: 0;
}

.btn.focus, .btn:focus,
.btn.active.focus, .btn.active:focus,
.btn:active.focus, .btn:active:focus {
	background: none;
	outline: 0;
}

/* Button types */

.btn.btn-round {
	-webkit-border-radius: 2px;
	   -moz-border-radius: 2px;
	     -o-border-radius: 2px;
	        border-radius: 2px;
}

.btn.btn-circle {
	-webkit-border-radius: 30px;
	   -moz-border-radius: 30px;
	     -o-border-radius: 30px;
	        border-radius: 30px;
}

/* Button colors */

.btn.btn-w {
	background: rgba(255, 255, 255, 0.8);
	color: #111;
}

.btn.btn-w:hover, .btn.btn-w:focus {
	background: white;
	color: #111;
}

.btn.btn-border-w {
	background: transparent;
	border: 1px solid rgba(255, 255, 255, 0.75);
	color: #fff;
}

.btn.btn-border-w:hover, .btn.btn-border-w:focus {
	background: #fff;
	border-color: transparent;
	color: #111;
}

.btn.btn-g {
	background: #eee;
	color: #111;
}

.btn.btn-g:hover, .btn.btn-g:focus {
	background: rgba(238, 238, 238, 0.7);
	color: #111;
}

.btn.btn-d {
	background: rgba(17, 17, 17, 0.8);
	color: #fff;
}

.btn.btn-d:hover, .btn.btn-d:focus {
	background: #111111;
}

.btn.btn-border-d {
	background: transparent;
	border: 2px solid #111;
	color: #111;
}

.btn.btn-border-d:hover {
	background: #111;
	color: #fff;
}

.btn.btn-b {
	background: #111111;
	color: #fff;
}

.btn.btn-b:hover, .btn.btn-b:focus {
	background: rgba(17, 17, 17, 0.8);
}

/* Button sizes */

.btn.btn-lg {
	font-size: 13px;
	padding: 12px 45px;
}

.btn.btn-sm {
	letter-spacing: 1px;
	font-size: 10px;
	padding: 6px 25px;
}

.btn.btn-xs {
	letter-spacing: 0;
	font-size: 10px;
	padding: 4px 19px;
}

/* ------------------------------------------------------------------
Progress bars
------------------------------------------------------------------- */

.progress {
	overflow: visible;
	height: 4px;
}

.progress-title {
	margin-top: 0;
}

.progress-bar {
	position: relative;
	-webkit-border-radius: 4px;
	   -moz-border-radius: 4px;
	    -ms-border-radius: 4px;
	     -o-border-radius: 4px;
	        border-radius: 4px;
}

.progress-bar.pb-dark {
	background: #111;
}

.progress-bar span {
	position: absolute;
	padding: 4px 0px;
	display: block;
	top: -28px;
	right: 0;
	opacity: 0;
	line-height: 12px;
	font-size: 12px;
	color: #777;
}

/* ------------------------------------------------------------------
Tabs and Accordion
------------------------------------------------------------------- */

.nav-tabs {
	border-color: #eee;
}

.nav-tabs > li > a {
	-webkit-border-radius: 2px;
	   -moz-border-radius: 2px;
	    -ms-border-radius: 2px;
	     -o-border-radius: 2px;
	        border-radius: 2px;
}

.nav-tabs >li.active > a,
.nav-tabs >li.active > a:hover,
.nav-tabs >li.active > a:focus {
	border: 1px solid #eee;
	border-bottom-color: transparent;
}

.nav-tabs > li > a:hover,
.nav-tabs > li > a:focus {
	background: #eee;
}

.tab-content {
	padding: 15px 0;
}

/* Accordion */

.panel-group {
	border: 1px solid #eee;
	-webkit-border-radius: 2px;
	   -moz-border-radius: 2px;
	    -ms-border-radius: 2px;
	     -o-border-radius: 2px;
	        border-radius: 2px;
}

.panel-group .panel {
	border-radius: 0;
}

.panel {
	border: 0;
	border-bottom: 1px solid #eee;
}

.panel-default > .panel-heading + .panel-collapse > .panel-body {
	border-color: #eee;
}

.panel-heading {
	padding: 15px 20px;
}

.panel-default > .panel-heading {
	background: transparent;
	border-color: #eee;
}

.panel-heading a {
	position: relative;
	display: block;
}

.panel-heading a:after {
	position: absolute;
	content: "\f106";
	top: 50%;
	right: 0px;
	margin-top: -7px;
	font-family: "FontAwesome";
	font-size: 14px;
	line-height: 1;
}

.panel-heading a.collapsed:after {
	content: "\f107";
}

.panel-title {
	font-size: 12px;
}

.panel-group .panel + .panel {
	border-top: 0;
	margin-top: 0;
}

/* -------------------------------------------------------------------
Price-tables
------------------------------------------------------------------- */

.price-table {
	background: #fff;
	border: 1px solid #eee;
	padding: 25px 20px;
	margin: 15px 0 30px;
	text-align: center;
	-webkit-border-radius: 2px;
	   -moz-border-radius: 2px;
	     -o-border-radius: 2px;
	        border-radius: 2px;
}

.price-table:hover {
	border-color: #eee;
}

.price-table.best {
	margin: 0 0 30px;
}

.price-table .small {
	margin: 0;
}

.borderline {
	position: relative;
	background: #eee;
	display: block;
	height: 1px;
	width: 100%;
	margin: 20px 0 15px;
}

.borderline:before {
	position: absolute;
	background: #eee;
	content: "";
	bottom: -7px;
	left: 50%;
	height: 14px;
	width: 14px;
	-webkit-transform: rotate(45deg);
	   -moz-transform: rotate(45deg);
	    -ms-transform: rotate(45deg);
	     -o-transform: rotate(45deg);
	margin-left: -7px;
}

.borderline:after {
	position: absolute;
	background: #fff;
	content: "";
	bottom: -5px;
	left: 50%;
	height: 16px;
	width: 16px;
	-webkit-transform: rotate(45deg);
	   -moz-transform: rotate(45deg);
	    -ms-transform: rotate(45deg);
	     -o-transform: rotate(45deg);
	margin-left: -8px;
}

.price-table h4 {
	color: #111;
	margin: 0;
}

.price-table p.price {
	font-size: 60px;
	color: #111;
	padding: 0;
	margin: 0 0 0 -10px;
}

.price-table p.price span {
	display: inline-block;
	vertical-align: top;
	font-size: 16px;
	padding-top: 25px;
}

.price-details {
	list-style: none;
	padding: 0;
	margin: 0 0 23px;
}

.price-details li {
	padding: 7px 0;
}

.price-details li > span {
	text-decoration: line-through;
	color: #aaa;
}

/* ------------------------------------------------------------------
Pagination
------------------------------------------------------------------- */

.pagination > li {
	display: inline-block;
}

.pagination > li > a,
.pagination > li > span {
	background: #f7f7f7;
	border: 0;
	width: 38px;
	height: 38px;
	padding: 0;
	margin: 0;
	text-align: center;
	line-height: 38px;
	color: #111;
	-webkit-border-radius: 2px;
	   -moz-border-radius: 2px;
	     -o-border-radius: 2px;
	        border-radius: 2px;
}

.pagination > .active > a,
.pagination > .active > span,
.pagination > .active > a:hover,
.pagination > .active > span:hover,
.pagination > .active > a:focus,
.pagination > .active > span:focus {
	background: #111;
}

.pagination > li > a:hover,
.pagination > li > span:hover,
.pagination > li > a:focus,
.pagination > li > span:focus {
	background: #f0f0f0;
	color: #111;
}

/* ------------------------------------------------------------------
Social links
------------------------------------------------------------------- */

.social-text-links,
.social-icon-links {
	list-style: none;
	padding: 0;
	margin: 0;
}

.socicon-round > li > a {
	-webkit-border-radius: 2px;
	   -moz-border-radius: 2px;
	     -o-border-radius: 2px;
	        border-radius: 2px;
}

.socicon-circle > li > a {
	-webkit-border-radius: 100%;
	   -moz-border-radius: 100%;
	     -o-border-radius: 100%;
	        border-radius: 100%;
}

.social-text-links > li {
	display: inline-block;
	margin: 0 10px 20px;
}

.social-icon-links > li {
	display: inline-block;
	margin: 0 5px 10px 0;
}

.social-icon-links > li > a {
	display: inline-block;
	background: #f7f7f7;
	height: 36px;
	width: 36px;
	text-align: center;
	line-height: 36px;
	font-size: 14px;
	color: #111;
}

.social-icon-links > li > a:hover {
	background: #f0f0f0;
}

/* ------------------------------------------------------------------
Content box
------------------------------------------------------------------- */

.content-box {
	margin: 25px 0;
}

.content-box-icon {
	text-align: center;
	font-size: 42px;
	color: #111;
}

.content-box-title {
	letter-spacing: 6px;
	text-align: center;
	margin: 5px 0 18px;
}

/* Alt content box */

.alt-content-box {
	position: relative;
	padding-left: 55px;
	margin: 25px 0;
}

.alt-content-box-icon {
	position: absolute;
	height: 38px;
	width: 38px;
	left: 0;
	top: 0;
	text-align: center;
	line-height: 38px;
	font-size: 28px;
	color: #111;
}

.alt-content-box-title {
	margin-bottom: 18px;
}

/* ------------------------------------------------------------------
Gallery box
------------------------------------------------------------------- */

.gallery-box {
	text-align: center;
	margin: 35px 0;
}

.gallery-box-text {
	font-size: 12px;
}

.gallery-box .gallery {
	display: block;
	margin: 20px 0 0;
}

.gallery-box .gallery:after {
	background: rgba(255, 255, 255, 0.3);
	position: absolute;
	content: "";
	left: 0;
	top: 0;
	height: 100%;
	width: 100%;
	opacity: 0;
	-webkit-transition: all 0.3s cubic-bezier(0.300, 0.100, 0.580, 1.000);
	   -moz-transition: all 0.3s cubic-bezier(0.300, 0.100, 0.580, 1.000);
	     -o-transition: all 0.3s cubic-bezier(0.300, 0.100, 0.580, 1.000);
	        transition: all 0.3s cubic-bezier(0.300, 0.100, 0.580, 1.000);
}

.gallery-box:hover .gallery:after {
	opacity: 1;
}

/* ------------------------------------------------------------------
Counters
------------------------------------------------------------------- */

.counter-item {
	text-align: center;
}

.counter-number {
	font-size: 20px;
}

/* ------------------------------------------------------------------
Team
------------------------------------------------------------------- */

.team-image {
	border-bottom: 1px solid #eee;
	position: relative;
	overflow: hidden;
}

.team-image > img {
	width: 100%;
	-webkit-transition: all 0.3s cubic-bezier(0.300, 0.100, 0.580, 1.000);
	   -moz-transition: all 0.3s cubic-bezier(0.300, 0.100, 0.580, 1.000);
	     -o-transition: all 0.3s cubic-bezier(0.300, 0.100, 0.580, 1.000);
	        transition: all 0.3s cubic-bezier(0.300, 0.100, 0.580, 1.000);
}

.team-detail {
	position: absolute;
	bottom: 0;
	left: 0;
	width: 100%;
	opacity: 0;
	-webkit-transform: translateY(50%);
	   -moz-transform: translateY(50%);
	        transform: translateY(50%);
	-webkit-transition: all 0.3s cubic-bezier(0.300, 0.100, 0.580, 1.000);
	   -moz-transition: all 0.3s cubic-bezier(0.300, 0.100, 0.580, 1.000);
	     -o-transition: all 0.3s cubic-bezier(0.300, 0.100, 0.580, 1.000);
	        transition: all 0.3s cubic-bezier(0.300, 0.100, 0.580, 1.000);
}

.team-item:hover .team-detail {
	opacity: 1;
	-webkit-transform: translateY(0%);
	   -moz-transform: translateY(0%);
	        transform: translateY(0%);
}

.team-item:hover .team-image > img {
	opacity: 0.5;
}

.team-descr {
	text-align: center;
	padding-top: 15px;
}

.team-name {
	letter-spacing: 6px;
	margin: 0 0 10px;
}

.team-role {
	font-size: 12px;
}

/* ------------------------------------------------------------------
Client logos
------------------------------------------------------------------- */

.client-item {
	position: relative;
	border-top: 1px solid #eee;
	border-right: 1px solid #eee;
	padding: 0;
	text-align: center;
	-webkit-transition: all 0.3s cubic-bezier(0.300, 0.100, 0.580, 1.000);
	   -moz-transition: all 0.3s cubic-bezier(0.300, 0.100, 0.580, 1.000);
	     -o-transition: all 0.3s cubic-bezier(0.300, 0.100, 0.580, 1.000);
	        transition: all 0.3s cubic-bezier(0.300, 0.100, 0.580, 1.000);
}

.client-item a {
	display: block;
	width: 100%;
	padding: 50px 15px;
	opacity: 0.9;
}

.client-item a:hover {
	background-color: rgba(0, 0, 0, 0.05);
	opacity: 1;
}

.client-item:before {
	position: absolute;
	border-left: 1px solid #eee;
	height: 100%;
	content: "";
	top: 0;
	left: -1px;
}

.client-item:after {
	position: absolute;
	border-bottom: 1px solid #eee;
	width: 100%;
	content: "";
	bottom: -1px;
	left: 0;
}

.quote-author {
	font-size: 12px;
	opacity: 0.75;
}

/* ------------------------------------------------------------------
Owl sliders
------------------------------------------------------------------- */

.owl-carousel .owl-controls .owl-buttons .owl-prev,
.owl-carousel .owl-controls .owl-buttons .owl-next {
	position: absolute;
	background: #fff;
	display: block;
	height: 40px;
	width: 40px;
	top: 50%;
	margin: -20px 10px 0;
	padding: 0;
	opacity: 0;
	text-align: center;
	line-height: 40px;
	font-size: 17px;
	color: #111;
	-webkit-border-radius: 2px;
	   -moz-border-radius: 2px;
	        border-radius: 2px;
	-webkit-transition: all 0.3s cubic-bezier(0.300, 0.100, 0.580, 1.000);
	   -moz-transition: all 0.3s cubic-bezier(0.300, 0.100, 0.580, 1.000);
	     -o-transition: all 0.3s cubic-bezier(0.300, 0.100, 0.580, 1.000);
	        transition: all 0.3s cubic-bezier(0.300, 0.100, 0.580, 1.000);
}

.owl-carousel .owl-controls .owl-buttons .owl-prev {
	left: -20px;
}

.owl-carousel .owl-controls .owl-buttons .owl-next{
	right: -20px;
}


.owl-carousel:hover .owl-controls .owl-buttons .owl-prev {
	opacity: 0.5;
	left: 0px;
}

.owl-carousel:hover .owl-controls .owl-buttons .owl-next {
	opacity: 0.5;
	right: 0px;
}

/* Pagination */

.owl-pagination {
	position: absolute;
	z-index: 3;
	margin-top: 15px;
	text-align: center;
	width: 100%;
}

.slider-testimonials .owl-pagination {
	bottom: -50px;
	margin: 0;
}

/* Slider images */

.slider-images .owl-pagination {
	bottom: 10px;
	margin: 0;
}

.owl-page {
	position: relative;
	background: rgba(255, 255, 255, 0.7);
	display: inline-block;
	overflow: hidden;
	height: 6px;
	width: 6px;
	margin: 6px 4px;
	text-indent: -200%;
	z-index: 1000;
	-webkit-border-radius: 6px;
	   -moz-border-radius: 6px;
	        border-radius: 6px;
	-webkit-box-shadow: 0 0 1px rgba(17, 17, 17, 0.4);
	   -moz-box-shadow: 0 0 1px rgba(17, 17, 17, 0.4);
	     -o-box-shadow: 0 0 1px rgba(17, 17, 17, 0.4);
	        box-shadow: 0 0 1px rgba(17, 17, 17, 0.4);
	-webkit-transition: all 0.3s cubic-bezier(0.000, 0.000, 0.580, 1.000);
	   -moz-transition: all 0.3s cubic-bezier(0.000, 0.000, 0.580, 1.000);
	     -o-transition: all 0.3s cubic-bezier(0.000, 0.000, 0.580, 1.000);
	        transition: all 0.3s cubic-bezier(0.000, 0.000, 0.580, 1.000);
}

.owl-page.active {
	-webkit-transform: scale(1.3);
	   -moz-transform: scale(1.3);
	     -o-transform: scale(1.3);
	        transform: scale(1.3);
}

/* ------------------------------------------------------------------
Overlays
------------------------------------------------------------------- */

.bg-light {
	background-color: #f7f7f7;
	height: 100%;
	width: 100%;
}

.bg-dark {
	background-color: #111;
	height: 100%;
	width: 100%;
}

.bg-dark,
.bg-dark-30,
.bg-dark-60,
.bg-dark-90,
.bg-dark h1, .bg-dark h2, .bg-dark h3, .bg-dark h4, .bg-dark h5, .bg-dark h6,
.bg-dark-30 h1, .bg-dark-30 h2, .bg-dark-30 h3, .bg-dark-30 h4, .bg-dark-30 h5, .bg-dark-30 h6,
