.navbar-light .show-overlay .icon-bar {
	background: #111;
}

.navbar-light .show-overlay:hover .icon-bar {
	background: rgba(17, 17, 17, 0.7);
}

/* Overlay menu */

.overlay-menu {
	position: fixed;
	background: rgba(0, 0, 0, 0.9);
	display: table;
	min-height: 100%;
	height: 100%;
	width: 100%;
	visibility: hidden;
	z-index: 9999;
	opacity: 0;
}

.overlay-menu.active {
	visibility: visible;
	opacity: 1;
	top: 0;
}


/* Hide menu link */

.navigation-hide {
	position: absolute;
	right: 20px;
	top: 20px;
	height: 40px;
	width: 40px;
	text-align: center;
	line-height: 40px;
	color: #fff;
}

.navigation-hide:hover {
	color: rgba(255, 255, 255, 0.7);
}

.overlay-menu-inner {
	vertical-align: middle;
	display: table-cell;
	height: 100%;
	width: 100%;
	text-align: center;
}

.overlay-menu-nav {
	max-width: 300px;
	margin: 0 auto 60px;
}

.overlay-navigation-footer {
	font-size: 1.2rem;
	position: absolute;
	left: 0;
	bottom: 18px;
	width: 100%;
	color: #666;
}

/* Overlay navbar */

#nav,
#nav ul {
	list-style: none;
	padding: 0;
	margin: 0;
}

#nav > li > a {
	font-family: "Dosis", sans-serif;
	text-transform: uppercase;
	letter-spacing: 3px;
	font-size: 24px;
	color: #FDFDFD;
}

#nav li a:hover, #nav li a.active {
	color: #fff;
}

#nav li ul {
	display: none;
	padding: 5px 0;
	margin: 5px 0 10px;
}

#nav li ul li a {
	display: inline-block;
	padding: 6px 0;
	text-transform: uppercase;
	letter-spacing: 2px;
	font-size: 11px;
	color: #666;
}

/* ------------------------------------------------------------------
Hero
------------------------------------------------------------------- */

.module-hero {
	position: relative;
	background-repeat: no-repeat;
	background-position: center center;
	-webkit-background-size: cover;
	   -moz-background-size: cover;
	     -o-background-size: cover;
	        background-size: cover;
}

.hero-caption {
	display: table;
	height: 100%;
	width: 100%;
	text-align: center;
}

.hero-text {
	display: table-cell;
	vertical-align: middle;
	height: 100%;
}

/* Hero caption sizes*/

.mh-line-size-1,
.mh-line-size-2,
.mh-line-size-3,
.mh-line-size-4,
.mh-line-size-5,
.mh-line-size-6 {
	font-weight: 400;
	margin: 0;
}

.mh-line-size-1 {
	letter-spacing: 50px;
	font-size: 30px;
	opacity: 0.9;
}

.mh-line-size-2 {
	letter-spacing: 40px;
	font-size: 26px;
	opacity: 0.9;
}

.mh-line-size-3 {
	letter-spacing: 24px;
	font-size: 26px;
	opacity: 0.8;
}

.mh-line-size-4 {
	letter-spacing: 10px;
	font-size: 16px;
	opacity: 0.6;
}

.mh-line-size-5 {
	letter-spacing: 3px;
	font-size: 14px;
	opacity: 0.6;
}

.mh-line-size-6 {
	font-size: 15px;
	opacity: 0.6;
}

/* ------------------------------------------------------------------
Modules
------------------------------------------------------------------- */

.wrapper {
	background: #fff;
	overflow: hidden;
	margin: 0 auto;
	width: 100%;
}

.module,
.module-small {
	padding: 140px 0;
	position: relative;
	background-repeat: no-repeat;
	background-position: center center;
	-webkit-background-size: cover;
	   -moz-background-size: cover;
	     -o-background-size: cover;
	        background-size: cover;
}

.module-small {
	padding: 70px 0;
}

.navbar-custom + .module {
	padding: 200px 0;
}

.navbar-custom + .module-small {
	padding: 130px 0;
}

.module-parallax {
	background-attachment: fixed;
}

/* Module titles */

.module-title {
	text-transform: uppercase;
	letter-spacing: 10px;
	text-align: center;
	font-size: 18px;
	margin-bottom: 70px;
}

.module-subtitle {
	text-align: center;
	margin-bottom: 70px;
}

.module-icon {
	font-size: 48px;
	margin-bottom: 70px;
}

.module-title + .module-subtitle {
	margin-top: -35px;
}

.divider {
	margin: 0;
}

.copyright {
	padding: 0 5px;
}

/* ------------------------------------------------------------------
Works grid
------------------------------------------------------------------- */

.works-grid {
	position: relative;
	width: 100%;
}

.work-item,
.grid-sizer {
	width: 25%;
}

.work-item.wide,
.work-item.wide-tall {
	width: 50%;
}

.show-more {
	font-size: 11px !important;
}

/* ------------------------------------------------------------------
Works item
------------------------------------------------------------------- */

.work-item a {
	position: absolute;
	top: 0;
	bottom: 30px;
	right: 15px;
	left: 15px;
	overflow: hidden;
}

.work-item a:after {
	position: absolute;
	display: block;
	content: "";
	top: 0;
	left: 0;
	width: 100%;
	height: 100%;
	-webkit-transition: all 0.3s cubic-bezier(0.300, 0.100, 0.580, 1.000);
	   -moz-transition: all 0.3s cubic-bezier(0.300, 0.100, 0.580, 1.000);
	     -o-transition: all 0.3s cubic-bezier(0.300, 0.100, 0.580, 1.000);
	        transition: all 0.3s cubic-bezier(0.300, 0.100, 0.580, 1.000);
}

.work-item img {
	height: auto;
	width: 100%;
}

.work-caption {
	position: absolute;
	left: 0;
	top: 100%;
	width: 100%;
	opacity: 0;
	overflow: hidden;
	padding: 20px;
	z-index: 3;
	text-align: center;
	-webkit-transition: all 0.3s cubic-bezier(0.300, 0.100, 0.580, 1.000);
	   -moz-transition: all 0.3s cubic-bezier(0.300, 0.100, 0.580, 1.000);
	     -o-transition: all 0.3s cubic-bezier(0.300, 0.100, 0.580, 1.000);
	        transition: all 0.3s cubic-bezier(0.300, 0.100, 0.580, 1.000);
}

.work-title {
	font-size: 14px;
	color: #fff;
	margin: 6px 0 6px;
}

.work-descr {
	font-size: 11px;
	color: rgba(255, 255, 255, 0.75);
}

/* White hover */

.works-hover-w .work-title {
	color: #111;
}

.works-hover-w .work-descr {
	color: rgba(17, 17, 17, 0.75);
}

/* ------------------------------------------------------------------
Works item hover
------------------------------------------------------------------- */

.work-item:hover a:after {
	background: rgba(34, 34, 34, 0.75);
	background: rgba(0, 0, 0, 0.8);
}

.works-hover-w .work-item:hover a:after {
	background: rgba(255, 255, 255, 0.8);
}

.work-item:hover .work-caption {
	opacity: 1;
	top: 50%;
	-webkit-transform: translateY(-50%);
	   -moz-transform: translateY(-50%);
	        transform: translateY(-50%);
}

/* ------------------------------------------------------------------
Portfolio Filter
------------------------------------------------------------------- */

.filters {
	text-align: center;
	list-style: none;
	padding: 0;
	margin: 0 0 70px;
}

.filters > li {
	display: inline-block;
	margin: 5px 30px;
}

.filters > li sup {
	top: -.7em;
	font-size: 80%;
	color: #999;
}

.filters > li > a {
	text-transform: uppercase;
	color: #111;
}

.filters > li > a:hover, .filters > li > a.current,
.filters > li > a:hover sup, .filters > li > a.current sup {
	color: #999;
}

/* ------------------------------------------------------------------
Portfolio Single
------------------------------------------------------------------- */

.image-caption {
	position: relative;
	margin: 0 0 20px;
}

.image-caption .caption-text {
	position: absolute;
	display: table;
	bottom: 0;
	height: 100%;
	width: 100%;
	z-index: 2;
	padding: 20px;
	font-size: 12px;
}

.project-details {
	list-style: none;
	padding: 0;
	margin: 0;
}

.project-details li {
	padding: 0 0 10px;
}

/* ------------------------------------------------------------------
Blog
------------------------------------------------------------------- */

/* Fix Firefox 3 column width bug */

@-moz-document url-prefix() {

	@media (min-width: 1310px) {
		.posts-masonry .col-lg-4 {
			width: 33.3333%;
		}
	}

	@media (min-width: 992px) {
		.posts-masonry .col-md-4 {
			width: 33.3333%;
		}
	}

}

.post {
	width: 100%;
}

.post-media {
	position: relative;
	margin: 0 0 20px;
}

.post-media img {
	width: 100%;
}

.post-media a:after {
	background: rgba(255, 255, 255, 0.3);
	position: absolute;
	content: "";
	left: 0;
	top: 0;
	height: 100%;
	width: 100%;
	opacity: 0;
	-webkit-transition: all 0.3s cubic-bezier(0.300, 0.100, 0.580, 1.000);
	   -moz-transition: all 0.3s cubic-bezier(0.300, 0.100, 0.580, 1.000);
	     -o-transition: all 0.3s cubic-bezier(0.300, 0.100, 0.580, 1.000);
	        transition: all 0.3s cubic-bezier(0.300, 0.100, 0.580, 1.000);
}

.post-media:hover a:after {
	opacity: 1;
}

.post-title {
	margin: 0;
}

.post-title a {
	color: #111;
}

.post-title a:hover {
	color: rgba(34, 34, 34, 0.55);
}

.post-meta {
	font-size: 12px;
	margin: 0 0 10px;
}

.post-header {
	margin: 0 0 20px;
}

/* Single post */

.post-single .post-media,
.post-single .post-header,
.post-single .post-entry {
	margin: 0 0 40px;
}

.post-single .post-meta {
	margin: 0 0 20px;
}

/* ------------------------------------------------------------------
Tags
------------------------------------------------------------------- */

.tags a {
	background: #f7f7f7;
	position: relative;
	display: inline-block;
	padding: 1px 15px 1px 10px;
	margin: 0px 15px 10px 0;
	height: 26px;
	letter-spacing: 1px;
	line-height: 24px;
	font-size: 11px;
	-webkit-border-radius: 2px 0 0 2px;
	   -moz-border-radius: 2px 0 0 2px;
	        border-radius: 2px 0 0 2px;
	-webkit-transition: all 0.3s cubic-bezier(0.300, 0.100, 0.580, 1.000);
	   -moz-transition: all 0.3s cubic-bezier(0.300, 0.100, 0.580, 1.000);
	     -o-transition: all 0.3s cubic-bezier(0.300, 0.100, 0.580, 1.000);
	        transition: all 0.3s cubic-bezier(0.300, 0.100, 0.580, 1.000);
}

.tags a:before {
	border-left: 8px solid #f7f7f7;
	border-top: 13px solid rgba(0, 0, 0, 0);
	border-bottom: 13px solid rgba(0, 0, 0, 0);
	position: absolute;
	content: "";
	right: -8px;
	top: 0;
	height: 0;
	width: 0;
	-webkit-transition: all 0.3s cubic-bezier(0.300, 0.100, 0.580, 1.000);
	   -moz-transition: all 0.3s cubic-bezier(0.300, 0.100, 0.580, 1.000);
	     -o-transition: all 0.3s cubic-bezier(0.300, 0.100, 0.580, 1.000);
	        transition: all 0.3s cubic-bezier(0.300, 0.100, 0.580, 1.000);
}

.tags a:after {
	background: #fff;
	position: absolute;
	display: block;
	content: "";
	right: 4px;
	top: 11px;
	height: 4px;
	width: 4px;
	-webkit-border-radius: 50%;
	   -moz-border-radius: 50%;
	        border-radius: 50%;
}

.tags a:hover {
	background: #f0f0f0;
}
.tags a:hover:before {
	border-left-color: #f0f0f0;
}

/* ------------------------------------------------------------------
Post author
------------------------------------------------------------------- */

.post-author .author-avatar {
	max-width: 70px;
	float: left;
}

.post-author .author-avatar img {
	-webkit-border-radius: 50%;
	   -moz-border-radius: 50%;
	        border-radius: 50%;
}

.post-author .author-content {
	margin-left: 100px;
}

.post-author .author-name {
	margin-top: 0;
}

/* ------------------------------------------------------------------
Comments and Comment form
------------------------------------------------------------------- */

.comment-avatar {
	float: left;
	max-width: 70px;
}

.comment-avatar img {
	-webkit-border-radius: 50%;
	   -moz-border-radius: 50%;
	        border-radius: 50%;
}

.comment-content {
	margin-left: 100px;
	margin-bottom: 30px;
}

.comment-author {
	margin: 0 0 20px;
}

.comment-author a {
	color: #111;
}

.comments,
.comment-form,
.post-author {
	margin: 80px 0 0;
}

.comment-title,
.comment-form-title,
.post-author-title {
	margin: 0 0 15px;
}

/* ------------------------------------------------------------------
Widgets
------------------------------------------------------------------- */

.widget {
	margin: 0 0 60px;
}

.widget-title {
	border-bottom: 1px solid #eee;
	padding: 0 0 10px;
	margin: 0 0 20px;
}

.widget ul {
	list-style: none;
	padding: 0;
	margin: 0;
}

.widget li {
	padding-bottom: 10px;
}

/* Search */

.search-box {
	position: relative;
}

.search-btn {
	background: transparent;
	border: none;
	position: absolute;
	overflow: hidden;
	right: 1px;
	top: 50%;
	height: 40px;
	width: 42px;
	outline: none;
	margin-top: -20px;
	line-height: 38px;
	font-size: 14px;
	color: #eee;
}

/* Recent posts */

.widget-posts li {
	padding-bottom: 20px;
}

.widget-posts-image {
	float: left;
	width: 64px;
}

.widget-posts-body {
	margin-left: 74px;
}

.widget-posts-title {
	margin: 0 0 5px;
}

.widget-posts-meta {
	letter-spacing: 1px;
	font-size: 11px;
}

/* -------------------------------------------------------------------
Google map
------------------------------------------------------------------- */

#module-maps {
	position: relative;
	height: 450px;
	width: 100%;
}

#map {
	height: 100%;
	width: 100%;
}

#map img {
	max-width: none;
}

/* ------------------------------------------------------------------
Preloader
------------------------------------------------------------------- */

.page-loader {
	background: #fff;
	position: fixed;
	top: 0;
	bottom: 0;
	right: 0;
	left: 0;
	z-index: 9998;
}

.loader {
	background: #ddd;
	position: absolute;
	display: inline-block;
	height: 40px;
	width: 40px;
	left: 50%;
	top: 50%;
	margin: -20px 0 0 -20px;
	text-indent: -9999em;
	-webkit-border-radius: 100%;
	   -moz-border-radius: 100%;
	        border-radius: 100%;
	-webkit-animation-fill-mode: both;
	        animation-fill-mode: both;
	-webkit-animation: ball-scale 1s 0s ease-in-out infinite;
	        animation: ball-scale 1s 0s ease-in-out infinite;
}


@-webkit-keyframes ball-scale {

	0% {
		-webkit-transform: scale(0);
		   -moz-transform: scale(0);
		    -ms-transform: scale(0);
		     -o-transform: scale(0);
		        transform: scale(0);
	}

	100% {
		opacity: 0;
		-webkit-transform: scale(1);
		   -moz-transform: scale(1);
		    -ms-transform: scale(1);
		     -o-transform: scale(1);
		        transform: scale(1);
	}

}

@keyframes ball-scale {

	0% {
		-webkit-transform: scale(0);
		   -moz-transform: scale(0);
		    -ms-transform: scale(0);
		     -o-transform: scale(0);
		        transform: scale(0);
	}

	100% {
		opacity: 0;
		-webkit-transform: scale(1);
		   -moz-transform: scale(1);
		    -ms-transform: scale(1);
		     -o-transform: scale(1);
		        transform: scale(1);
	}

}

/* -------------------------------------------------------------------
Scroll to top
------------------------------------------------------------------- */

.scroll-up {
	position: fixed;
	display: none;
	bottom: 7px;
	right: 7px;
	z-index: 999;
}

.scroll-up a {
	background: #fff;
	display: block;
	height: 28px;
	width: 28px;
	text-align: center;
	line-height: 28px;
	font-size: 14px;
	color: #000;
	opacity: 0.6;
	-webkit-border-radius: 2px;
	   -moz-border-radius: 2px;
		 -o-border-radius: 2px;
			border-radius: 2px;
}

.scroll-up a:hover,
.scroll-up a:active {
	opacity: 1;
	color: #000;
}

/* ------------------------------------------------------------------
Reset bootstrap navigation styles for small screens
------------------------------------------------------------------- */

.navbar-custom .navbar-header {
	float: left;
}

.navbar-custom .navbar-right {
	float: right !important;
	margin-right: -15px;
}

.navbar-custom .navbar-nav {
	margin: 0 -15px;
}

.navbar-right + .extra-navbar {
	margin: 0 15px;
}

.navbar-custom .navbar-nav > li {
	float: left;
}

.navbar-custom .navbar-nav > li > a {
	padding-top: 15px;
	padding-bottom: 15px;
}

/* -------------------------------------------------------------------
Responsive Media Querries
------------------------------------------------------------------- */

@media (min-width: 768px) {

	/* Comments */

	.comment .comment {
		margin-left: 100px;
	}

}


@media (max-width: 992px) {

	/* Hero captions */

	.mh-line-size-1 {
		letter-spacing: 40px;
		font-size: 26px;
	}

	.mh-line-size-2 {
		letter-spacing: 28px;
		font-size: 20px;
	}

	.mh-line-size-3 {
		letter-spacing: 20px;
		font-size: 22px;
	}

	.mh-line-size-4 {
		letter-spacing: 8px;
		font-size: 16px;
	}

	/* Portfolio grid */

	.work-item,
	.grid-sizer {
		width: 33.3333%;
	}

	.work-item.wide,
	.work-item.wide-tall {
		width: 66.6666%;
	}

}

@media only screen and (max-width: 960px) {

	/* Side and overlay nav */
	.overlay-menu.active {
		position: absolute;
	}
	.aux-navigation-active .wrapper{
		position: fixed;
	}
	.aux-navigation-active .side-navigation-wrapper,
	.aux-navigation-active .overlay-menu {
		position: absolute;
	}

}

@media (max-width: 767px) {

	/* Hero captions */

	.mh-line-size-1 {
		letter-spacing: 24px;
		font-size: 26px;
	}

	.mh-line-size-2 {
		letter-spacing: 16px;
		font-size: 15px;
	}

	.mh-line-size-3 {
		letter-spacing: 14px;
		font-size: 14px;
	}

	.mh-line-size-4 {
		letter-spacing: 4px;
		font-size: 14px;
	}

	.mh-line-size-5 {
		letter-spacing: 2px;
		font-size: 13px;
	}

	.mh-line-size-6 {
		font-size: 13px;
	}

